/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Row, Col, Popconfirm, Modal, Spin, Typography, Select, Input, Tag, Tabs, message, Card, Statistic } from 'antd';
import { DeleteOutlined, EditOutlined, LoadingOutlined, PlusOutlined, ArrowDownOutlined, ArrowUpOutlined } from '@ant-design/icons';
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from 'react';
import orderService from '../../services/order';
import Pagination from '../components/Pagination';
import jobStageService from '../../services/jobStage';
import jobService from '../../services/job';


export default function Order() {

    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [qData, setQData] = useState({ page: 1, limit: 20 });
    const addNewModalRef = useRef();
    const jobOrderStatusModalRef = useRef();
    const columns = [
        {
            title: 'Name',
            dataIndex: 'name',
        },
        {
            title: 'Quantity',
            dataIndex: 'quantity',
            width: 80
        },
        {
            title: 'Order Job Status',
            dataIndex: '_id',
            width: 170,
            render: (v,row) => <> <Button type="primary" size='small' onClick={() => jobOrderStatusModalRef.current.openForm(row)}>Status</Button> </>
        },
        {
            title: 'Status',
            dataIndex: 'status',
            width: 80,
            render: status => {
                if (status == 'initial') {
                    return <Tag color='blue'>Initial</Tag>
                } else {
                    return <Tag color="yellow">Completed</Tag>
                }
            },
        },
        {
            title: 'Action',
            dataIndex: '_id',
            width: 70,
            render: (v, row) => {
                return <>
                    <Popconfirm
                        title="Are you sure to delete this Data?"
                        onConfirm={() => { deleteData(row._id); }}
                        okText="Yes"
                        cancelText="No"
                    >
                        <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
                    </Popconfirm>
                    <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row) }} />
                </>
            }
        },
    ].filter(item => !item.hidden);

    function list() {
        setLoading(true);
        orderService.list(qData).then(res => {
            setData(res.data?.map((v) => ({ ...v, key: v._id })));
            setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
        }).catch(err => { }).finally(() => {
            setLoading(false);
        });
    }

    const deleteData = (id) => {
        orderService.delete(id).then(res => {
            message.success(res.message);
            list();
        }).catch(err => {
            message.error(err.message);
        })
    }

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';

    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, deleteData, qData, setQData, list, }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        tableLayout: undefined,
    };

    useEffect(() => {
        list();
    }, [qData.page, qData.limit]);


    return (
        <>
            <Typography.Title level={5} style={{ marginTop: 0 }}>List of Order </Typography.Title>
            <Table
                {...tableProps}
                pagination={{ position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            />
            <AddForm ref={addNewModalRef} {...{ list }} />
            <JobOrderStatusModel ref={jobOrderStatusModalRef} />
        </>
    );
};

function Search({ addNewModalRef, qData, list }) {
    const onChange = (v, key) => {
        qData[key] = v;
        if (v === undefined || v === "") {
            delete qData[key];
            list();
        }
    };
    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col span={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Input placeholder="Search by name" value={qData.key} onChange={e => onChange(e.target.value, "key")} allowClear />
                    </Form.Item>
                </Col>
                <Col span={3}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Button type="primary" htmlType="submit">Search</Button>
                    </Form.Item>
                </Col>
                <Col span={5}>
                    <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                        <Button type="primary" icon={<PlusOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Add New</Button>
                    </Form.Item>
                </Col>
            </Row>
        </Form>
    );
};

const AddForm = forwardRef((props, ref) => {
    const { list } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);


    const handleChange = (value) => { setData({ ...data, ...value }); }

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(
                dt
                    ? { ...dt }
                    : { status: true }
            );
        }
    }));

    const save = () => {
        setAjxRequesting(true);
        orderService.save(data).then((res) => {
            message.success(res.message);
            handleChange({ ...res.data });
            list();
            setOpen(false);
        }).catch(err => {
            if (typeof err.message === 'object') {
                let dt = err.message[Object.keys(err.message)[0]];
                message.error(dt);
            } else {
                message.error(err.message);
            }
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    return (
        <>
            <Modal
                title={(!data._id ? 'Add' : 'Edit') + ' Order'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                keyboard={false}
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={600}
                className="app-modal-body"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    <Button key="save" type="primary" onClick={save}>Save</Button>,
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Form layout="vertical">
                        <Row gutter={[12, 2]}>
                            <Col span={24}>
                                <Row gutter={[12, 2]}>
                                    <Col span={24}>
                                        <Form.Item label=" Name" required>
                                            <Input placeholder="Name" value={data.name} onChange={e => { handleChange({ name: e.target.value }) }} />
                                        </Form.Item>
                                    </Col>
                                    <Col span={24}>
                                        <Form.Item label="Quantity" required>
                                            <Input placeholder="Quantity" type='number' value={data.quantity} onChange={e => { handleChange({ quantity: e.target.value }) }} />
                                        </Form.Item>
                                    </Col>
                                    <Col span={24}>
                                        <Form.Item label="Status" required>
                                            <Select value={data.status} onChange={e => { handleChange({ status: e }) }} >
                                                <Select.Option value="initial">Initial</Select.Option>
                                                <Select.Option value="completed">Completed</Select.Option>
                                            </Select>
                                        </Form.Item>
                                    </Col>
                                </Row>
                            </Col>

                        </Row>
                    </Form>
                </Spin>
            </Modal>
        </>
    );
});


const JobOrderStatusModel = forwardRef((props, ref) => {
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({});
    const [ajxRequesting, setAjxRequesting] = useState(false);
    const [order, setOrder] = useState()
    const [currentSubJobList, setCurrentSubJobList] = useState([])
    const [activeKey, setActiveKey] = useState('');

    console.log("this is dt",order)

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setOrder(dt)
        }
    }));

    function list() {
        setAjxRequesting(true);
        jobStageService.list().then(res => {
            setData(res.data);
        }).catch(err => { }).finally(() => {
            setAjxRequesting(false);
        });
    }

    useEffect(() => {
        list();
    }, [open]);

    useEffect(() => {
        setActiveKey(data[0]?._id);
    }, [data])


    return (
        <>
            <Modal
                title={"Job Order Status"}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={1000}
                footer={null}

            >
                <Spin spinning={ajxRequesting}>
                    {data.length > 0 &&
                        <Card size='small'>
                            <Tabs
                                activeKey={activeKey}
                                onChange={v => { setActiveKey(v); }}
                                type="card"
                                style={{ height: 300 }}
                                items={data.map((v) => ({
                                    label: `${v?.name}`,
                                    key: `${v?._id}`,
                                    children: <>
                                        <Card bordered={false} size="small">
                                            <SubJobsTabs subJobs={data.find(v => v?._id === activeKey)} {...{ order }}  />
                                        </Card>
                                    </>,
                                }))}
                            />
                        </Card>
                    }
                </Spin>
            </Modal>
        </>
    );
});

const SubJobsTabs = ({ subJobs,order }) => {

    const [listJobs, setListJobs] = useState([]);
    const [loading, setLoading] = useState(false);
    const [activeKey, setActiveKey] = useState('');
    // console.log("this is order Id",orderId)
    useEffect(() => {
        if (subJobs?._id) {
            setLoading(true);
            jobService.list({ jobStageId: subJobs?._id }).then(res => {
                setListJobs(res?.data)
            }).catch(err => { }).finally(() => {
                // setAjxRequesting(false);
            });
        } else {
            setListJobs([]);
        }
    }, []);

    useEffect(() => {
        if (listJobs.length) {
            setActiveKey(listJobs[0]?._id);
        }
    }, [listJobs]);

    return (
        <Tabs
            type="card"
            destroyInactiveTabPane={false}
            tabPosition={"left"}
            onChange={v => { setActiveKey(v); }}
            activeKey={activeKey}
            items={[
                ...listJobs?.map(v => ({
                    label: v.jobName,
                    key: v._id,
                    children: <DataCard jobId={v._id} {...{ order }} subJobs={subJobs?._id} />
                }))
            ]}
        />
    );
}

const DataCard =({jobId,order,subJobs,})=>{
    const [ajxRequesting, setAjxRequesting] = useState(false);
    const [data,setData] = useState({})
    const orderModalRef = useRef();
    // console.log("this is order Id",)
    function list(){
        setAjxRequesting(true);
        orderService.listJobStageStatus({jobId:jobId,orderId:order?._id,jobStageId:subJobs}).then((res)=>{
            setData(res?.data[0])
        }).catch(err => { }).finally(() => {
            setAjxRequesting(false);
        });
    } 

    useEffect(() => {
     list()
    }, [jobId]);
    
    return(
        <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
        <Card style={{ backgroundColor: "rgb(240, 242, 245)" }}>
        <Row gutter={16}>
            <Col span={12}>
                <Card bordered={false} style={{height:'120px'}}>
                    <Statistic
                        title="Completed"
                        value={data?.status ==='completed' ? data?.quantity:0}
                        // precision={2}
                        valueStyle={{
                            color: '#3f8600',
                        }}
                        prefix={<ArrowUpOutlined />}
                        suffix="(Items)"
                    />
                </Card>
            </Col>
            <Col span={12}>
                <Card bordered={false} style={{height:'120px'}} >
                    <Statistic
                        title="Rejected"
                        value={data?.status ==='rejected' ? data?.quantity:0}
                        valueStyle={{
                            color: '#cf1322',
                        }}
                        prefix={<ArrowDownOutlined />}
                        suffix={<Button type="text" onClick={() => orderModalRef.current.openForm(order)} danger size1="small">Generate New Order</Button>}
                        
                    />
                    <span style={{marginTop:0,color:'gray'}}>{data?.rejectionMsg ?data.rejectionMsg : "" }</span>
                </Card>
                    <ReorderModel ref={orderModalRef} />
            </Col>
        </Row>
    </Card>
    </Spin>
    )
}

const ReorderModel = forwardRef((props, ref) => {
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({});
    const [orderData, setOrderData] = useState()
    const [ajxRequesting, setAjxRequesting] = useState(false);

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setOrderData(dt)
        }
    }));

    const handleChange = (value) => { setData({ ...data, ...value }); }

    const save = () => {
        setAjxRequesting(true);
        orderService.save({...data,parentId:orderData?._id,name:orderData?.name}).then((res) => {
            message.success(res.message);
            handleChange({ ...res.data });
            setOpen(false);
        }).catch(err => {
            if (typeof err.message === 'object') {
                let dt = err.message[Object.keys(err.message)[0]];
                message.error(dt);
            } else {
                message.error(err.message);
            }
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    return (
        <>
             <Modal
                title= "Re-Order Rejected"
                style={{ top: 20 }}
                open={open}
                okText="Save"
                keyboard={false}
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={600}
                className="app-modal-body"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    <Button key="save" type="primary" onClick={save}>Save</Button>,
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Form layout="vertical">
                        <Row gutter={[12, 2]}>
                            <Col span={24}>
                                <Row gutter={[12, 2]}>
                                    <Col span={24}>
                                        <Form.Item label="Quantity" required>
                                            <Input placeholder="Quantity" type='number' onChange={e => { handleChange({ quantity: e.target.value }) }} value={data.quantity}  />
                                        </Form.Item>
                                    </Col>
                                </Row>
                            </Col>
                        </Row>
                    </Form>
                </Spin>
            </Modal>
        </>
    );
});