/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Row, Col, Popconfirm, Modal, Spin, Typography, Select, Input, Tag, message } from 'antd';
import { DeleteOutlined, EditOutlined, LoadingOutlined, PlusOutlined } from '@ant-design/icons';
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from 'react';
import UserService from '../../services/user';
import jobStageService from '../../services/jobStage';
import Pagination from '../components/Pagination';


export default function UserManagement() {

    const [data, setData] = useState([]);
    const [jobStage, setjobStage] = useState([]);
    const [loading, setLoading] = useState(false);
    const [qData, setQData] = useState({ page: 1, limit: 20 });
    const addNewModalRef = useRef();
    const columns = [
        {
            title: 'Username',
            dataIndex: 'userName',
        },
        {
            title: 'Job Stage Assigned',
            dataIndex: 'jobStageDetails',
            width: 250,
            render: (v) => (v.name)
        },
        {
            title: 'Status',
            dataIndex: 'status',
            width: 60,
            render: status => {
                if (status === "blocked") {
                    return <Tag color='red'>Blocked</Tag>
                } else if (status === "active") {
                    return <Tag color='green'>Active</Tag>
                } else {
                    return <Tag color="yellow">Inactive</Tag>
                }
            },
        },
        {
            title: 'Action',
            dataIndex: '_id',
            width: 70,
            render: (v, row) => {
                return <>
                    <Popconfirm
                        title="Are you sure to delete this User?"
                        onConfirm={() => { deleteData(row._id); }}
                        okText="Yes"
                        cancelText="No"
                    >
                        <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
                    </Popconfirm>
                    <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row) }} />
                </>
            }
        },
    ].filter(item => !item.hidden);

    function list() {
        setLoading(true);
        UserService.listUser(qData).then(res => {
            setData(res.data?.map((v) => ({ ...v, key: v._id })));
            setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
        }).catch(err => { }).finally(() => {
            setLoading(false);
        });
    }

    function jobStageList() {
        setLoading(true);
        jobStageService.list({ isAll: 1 }).then(res => { setjobStage(res.data || []); }).catch(err => { message.error('Job Stage data not loaded'); });;
    }

    const deleteData = (id) => {
        UserService.deleteUser(id).then(res => {
            message.success(res.message);
            list();
        }).catch(err => {
            message.error(err.message);
        })
    }

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

    tableColumns[0].fixed = true;
    // tableColumns[tableColumns.length - 1].fixed = 'right';

    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, deleteData, qData, setQData, list, }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        tableLayout: undefined,
    };

    useEffect(() => {
        list();
        jobStageList()
    }, [qData.page, qData.limit]);


    return (
        <>
            <Typography.Title level={5} style={{ marginTop: 0 }}>List of Users</Typography.Title>
            <Table
                {...tableProps}
                pagination={{ position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            />
            <AddForm ref={addNewModalRef} {...{ list, jobStage }} />
        </>
    );
};

function Search({ addNewModalRef, qData, list, }) {
    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col span={5}>
                    <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                        <Button type="primary" icon={<PlusOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Add New</Button>
                    </Form.Item>
                </Col>
            </Row>
        </Form>
    );
};

const AddForm = forwardRef((props, ref) => {
    const { list, jobStage } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);

    const handleChange = (value) => { setData({ ...data, ...value }); }

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(
                dt
                    ? { ...dt }
                    : { status: true }
            );
        }
    }));

    const save = () => {
        setAjxRequesting(true);
        UserService.saveUser(data).then((res) => {
            message.success(res.message);
            handleChange({ ...res.data });
            list();
            setOpen(false);
        }).catch(err => {
            if (typeof err.message === 'object') {
                let dt = err.message[Object.keys(err.message)[0]];
                message.error(dt);
            } else {
                message.error(err.message);
            }
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    return (
        <>
            <Modal
                title={(!data._id ? 'Add' : 'Edit') + ' User'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                keyboard={false}
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={800}
                className="app-modal-body"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    <Button key="save" type="primary" onClick={save}>Save</Button>,
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Form layout="vertical">
                        <Row gutter={[12, 2]}>
                            <Col span={24}>
                                <Row gutter={[12, 2]}>
                                    <Col span={12}>
                                        <Form.Item label="Name" required>
                                            <Input placeholder="John Doe" value={data.name} onChange={e => { handleChange({ name: e.target.value }) }} />
                                        </Form.Item>
                                    </Col>
                                    <Col span={12}>
                                        <Form.Item label="Username" required>
                                            <Input placeholder="John Doe" value={data.userName} onChange={e => { handleChange({ userName: e.target.value }) }} />
                                        </Form.Item>
                                    </Col>
                                    <Col span={12}>
                                        <Form.Item label={data?._id ? "Update Password" : "Set Password"} required={data?._id ? false : true}>
                                            <Input type='password' value={data?.password} onChange={e => { handleChange({ password: e.target.value }) }} />
                                        </Form.Item>
                                    </Col>
                                    <Col span={12}>
                                        <Form.Item label="Select Job Stage" required>
                                            <Select value={data.jobStageId} onChange={v => { handleChange({ jobStageId: v }) }}
                                                options={
                                                    jobStage.map(v => (
                                                        {
                                                            value: v._id,
                                                            label: v.name,
                                                        }
                                                    ))
                                                }
                                            >
                                            </Select>
                                        </Form.Item>
                                    </Col>
                                    <Col span={12}>
                                        <Form.Item label="Status" required>
                                            <Select value={data.status} onChange={e => { handleChange({ status: e }) }} >
                                                <Select.Option value="active">Active</Select.Option>
                                                <Select.Option value="inactive">Inactive</Select.Option>
                                                <Select.Option value="blocked">Blocked</Select.Option>
                                            </Select>
                                        </Form.Item>
                                    </Col>
                                </Row>
                            </Col>
                        </Row>
                    </Form>
                </Spin>
            </Modal>
        </>
    );
});