import { DashboardOutlined, BankOutlined, ApartmentOutlined, CalendarOutlined } from '@ant-design/icons';

import Dashboard from '../pages/Dashboard';
import UserManagement from '../pages/UserManagement';
import JobStage from '../pages/JobStage';
import Order from '../pages/Order';


const routes = [
    { title: 'Dashboard', icon: DashboardOutlined, url: '/', component: Dashboard },
    { title: 'User', icon: ApartmentOutlined, url: '/users', component: UserManagement },
    { title: 'Job Stage', icon: ApartmentOutlined, url: '/job-stage', component: JobStage },
    { title: 'Order', icon: ApartmentOutlined, url: '/order', component: Order },
].filter(v => (!v.hidden) || v.children?.length);

export default routes;