/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Row, Col, Popconfirm, Modal, Spin, Typography, message, Input } from 'antd';
import { DeleteOutlined, EditOutlined, LoadingOutlined, PlusOutlined } from '@ant-design/icons';
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from 'react';
import jobStageService from '../../services/jobStage';
import Pagination from '../components/Pagination';
import Job from './Job';


export default function JobStage() {

    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [qData, setQData] = useState({ page: 1, limit: 20 });
    const addNewModalRef = useRef();
    const ModelJobRef = useRef();
    const columns = [
        {
            title: 'Stage No',
            dataIndex: 'stageNo',
            width: 100
        },
        {
            title: 'Job Stage',
            dataIndex: 'name',
        },
        {
            title: 'Job',
            dataIndex: '_id',
            width: 50,
            render: (v, row) => <Button type="primary" size='small' onClick={() => { ModelJobRef.current.openForm({ _id: row._id, name: row.name }) }} >Job</Button>
        },
        {
            title: 'Action',
            dataIndex: '_id',
            width: 70,
            render: (v, row) => {
                return <>
                    <Popconfirm
                        title="Are you sure to delete this Data?"
                        onConfirm={() => { deleteData(row._id); }}
                        okText="Yes"
                        cancelText="No"
                    >
                        <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
                    </Popconfirm>
                    <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row) }} />
                </>
            }
        },
    ].filter(item => !item.hidden);

    function list() {
        setLoading(true);
        jobStageService.list(qData).then(res => {
            setData(res.data?.map((v) => ({ ...v, key: v._id })));
            setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
        }).catch(err => { }).finally(() => {
            setLoading(false);
        });
    }

    const deleteData = (id) => {
        jobStageService.delete(id).then(res => {
            message.success(res.message);
            list();
        }).catch(err => {
            message.error(err.message);
        })
    }

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

    tableColumns[1].fixed = true;

    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, deleteData, qData, setQData, list, }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        tableLayout: undefined,
    };

    useEffect(() => {
        list();
    }, [qData.page, qData.limit]);


    return (
        <>
            <Typography.Title level={5} style={{ marginTop: 0 }}>List of Job Stage</Typography.Title>
            <Table
                {...tableProps}
                pagination={{ position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            />
            <AddForm ref={addNewModalRef} {...{ list }} />
            <JobForm ref={ModelJobRef} {...{ list }} />
        </>
    );
};

function Search({ addNewModalRef, deleteData, qData, list, }) {
    const onChange = (v, key) => {
        qData[key] = v;
        if (v === undefined || v === "") {
            delete qData[key];
            list();
        }
    };
    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col span={5}>
                    <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                        <Button type="primary" icon={<PlusOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Add New</Button>
                    </Form.Item>
                </Col>
            </Row>
        </Form>
    );
};

const AddForm = forwardRef((props, ref) => {
    const { list } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);


    const handleChange = (value) => { setData({ ...data, ...value }); }

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(
                dt
                    ? { ...dt }
                    : { status: true }
            );
        }
    }));

    const save = () => {
        setAjxRequesting(true);
        jobStageService.save(data).then((res) => {
            message.success(res.message);
            handleChange({ ...res.data });
            list();
            setOpen(false);
        }).catch(err => {
            if (typeof err.message === 'object') {
                let dt = err.message[Object.keys(err.message)[0]];
                message.error(dt);
            } else {
                message.error(err.message);
            }
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    return (
        <>
            <Modal
                title={(!data._id ? 'Add' : 'Edit') + ' Job Stage'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                keyboard={false}
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={400}
                className="app-modal-body"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    <Button key="save" type="primary" onClick={save}>Save</Button>,
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Form layout="vertical">
                        <Row gutter={[12, 2]}>
                            <Col span={24}>
                                <Row gutter={[12, 2]}>
                                    <Col span={24}>
                                        <Form.Item label="Stage No" required>
                                            <Input placeholder="Stage No" type='number' value={data.stageNo} onChange={e => { handleChange({ stageNo: e.target.value }) }} />
                                        </Form.Item>
                                    </Col>
                                    <Col span={24}>
                                        <Form.Item label="Name" required>
                                            <Input placeholder="Name" value={data.name} onChange={e => { handleChange({ name: e.target.value }) }} />
                                        </Form.Item>
                                    </Col>
                                </Row>
                            </Col>

                        </Row>
                    </Form>
                </Spin>
            </Modal>
        </>
    );
});

const JobForm = forwardRef((props, ref) => {
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({});

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(dt ? { ...dt } : { status: true });
        }
    }));

    return (
        <>
            <Modal
                title={<>All Job List of <Typography.Text type="danger">{data?.name}</Typography.Text></>}
                style={{ top: 20 }}
                open={open}
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={600}
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                ]}
            >
                <Job {...{ jobStageId: data._id }} />
            </Modal>
        </>
    );
});